import React, {Component} from 'react';
import RemoveCircle from '@material-ui/icons/RemoveCircle';
class TeamMemberProfileComponent extends Component {
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
        this.state = {};
    }

    handleClick() {
        console.log("trying to click profile");
        this.props.showTeamMemberModal(this.props.member);
    }

    render() {
        const {member, showRemoval} = this.props;
        const {hide} = this.state;

        const btnImg = member.imgUrl || "data:image/png;base64,";

        if (hide) {
            return null;
        }
        return (
            <div className="team-member-profile-thumbnail">
                <button className="btn-floating waves-effect waves-light profile-image-circle-mini" onClick={this.handleClick}>
                    <img
                        aria-label="Click to view this team member"
                        alt="Team Member Profile Button"
                        style={{width: "100%"}}
                        src={btnImg}
                    />
                </button>
                <h4>{showRemoval && <button className="btn btn-link" onClick={() => this.setState({hide: true})}><RemoveCircle style={{color: "red"}} /></button>}{member.name}</h4>
            </div>
        );
    }
}

export default TeamMemberProfileComponent;
